@import "../../helpers/styles.scss";

.modal {
  background: white;
  margin: 7vh auto;
  position: relative;
  max-width: 70%;
  padding: 10px;
  border-radius: 4px;
  max-height: 90vh;
  overflow-x: hidden;
  overflow-y: auto;
  @include Responsive("tab") {
    max-width: 100% !important;
  }
}

.dialog {
  background: white;
  position: relative;
  border-radius: 4px;
  overflow-x: hidden;
  overflow-y: auto;
}
